<template>
  <div>
    <h2>商户管理</h2>
    <a-row type="flex" style="margin-bottom: 20px;">
      <a-col style="width: 100%; display: flex;">
        <!-- <div style="width: 100%;">
          <a-input v-model="searchParams.keyword" style="max-width: 200px; margin-right: 12px;" />
          <a-button type="primary">搜索</a-button>
        </div> -->
        <a-button type="primary" @click="popShop">添加商户</a-button>
      </a-col>
    </a-row>
    <s-table
      ref="table"
      bordered
      size="default"
      row-key="id"
      :columns="columns"
      :data="loadData"
    >
      <template slot="action" slot-scope="text">
        <!-- <a-button type="link" @click="toEdit(text.id)">编辑</a-button> -->
        <a-button type="link" @click="toDelete(text.id)">删除</a-button>
      </template>
    </s-table>

    <a-modal
      v-model="shopVisible"
      title="添加商户"
      width="60%"
      @ok="handleSubmit"
      :okButtonProps="{
        props: {
          loading: submitLoading
        }
      }"
    >
      <a-spin :spinning="modalLoading">
        <a-form>
          <a-form-item label="手机">
            <a-input v-model="form.phone" />
          </a-form-item>
          <a-form-item label="密码">
            <a-input v-model="form.password" />
          </a-form-item>
          <a-form-item label="名字">
            <a-input v-model="form.name" />
          </a-form-item>
          <a-form-item label="logo">
            <a-upload
              name="file"
              list-type="picture-card"
              class="avatar-uploader"
              :show-upload-list="false"
              :action="`${uploadPrefix}/upload`"
              @change="handleLogoChange"
            >
              <div
                v-if="form.logo"
                class="bg_img cover_img"
                :style="`background-image: url(${imgPrefix}${form.logo});`"
              ></div>
              <div v-else>
                <a-icon :type="logoLoading ? 'loading' : 'plus'" />
                <div class="ant-upload-text">
                  Upload
                </div>
              </div>
            </a-upload>
          </a-form-item>
        </a-form>
      </a-spin>
    </a-modal>
  </div>
</template>
<script>
import STable from '@/components/Table'
import { mapState, mapActions } from 'vuex'
const getDefaultSearchParams = () => ({
  keyword: ''
})
const getFormDefaultParams = () => ({
  phone : "",
  password : "",
  name : "",
  logo : ""
})
export default {
  components: { STable },
  data() {
    const columns = [
      {
        title: 'id',
        dataIndex: 'id'
      },
      {
        title: '手机号码',
        dataIndex: 'phone'
      },
      {
        title: '商户名称',
        dataIndex: 'shop.name'
      },
      {
        title: '操作',
        scopedSlots: { customRender: "action"},
      }
    ]
    return {
      columns,
      searchParams: getDefaultSearchParams(),
      form: getFormDefaultParams(),
      shopVisible: false,
      submitLoading: false,
      modalLoading: false,
      logoLoading: false,
      proxy: process.env.VUE_APP_API_URL,
      prefix: process.env.VUE_APP_PREFIX,
      imgPrefix: process.env.VUE_APP_IMG_PREFIX,
      uploadPrefix: process.env.VUE_APP_UPLOAD_PREFIX,
      loadData: paramters => {
        return this.getShopList(paramters)
      }
    }
  },
  methods: {
    ...mapActions('shop', {
      getShopList: 'getList',
      addShop: 'add',
      deleteShop: 'delete'
    }),
    toEdit(id) {

    },
    toDelete(id) {
      const _this = this
      this.$confirm({
        content: '你确定要删除这个商户？',
        okText: '确认',
        cancelText: '取消',
        onOk() {
          _this.deleteShop(id).then(res => {
            _this.$refs.table.refresh(true)
          })
        }
      });
    },
    popShop() {
      this.shopVisible = true
    },
    handleLogoChange(e) {
      const file = e.file
      if (file.status === 'uploading') {
        this.form.logo = ''
        this.logoLoading = true
      }
      if (file.status === 'done') {
        this.form.logo = file.response.data
        this.logoLoading = false
      }
    },
    handleSubmit() {
      this.submitLoading = true
      this.addShop(this.form).then(() => {
          this.$message.success('添加成功')
          this.$refs.table.refresh(true)
          this.shopVisible = false
        }).finally(() => { 
          this.submitLoading = false 
        })
    }
  }
}
</script>